@import url(https://fonts.googleapis.com/css2?family=Gugi);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right, #eecda3 , #ef629f);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.avatarstyle img {
	width: 200px;
	height: 200px;
}

.mainpage { width: 90%; height: 100vh; margin: auto; font-family: 'Gugi', cursive;}
.mainpage h1 { margin: 0; padding: 20px 0; text-align: center; text-decoration: underline overline; -webkit-text-decoration-style: double; text-decoration-style: double;}
.mainpage button {background: #990000; text-align: center; padding: 15px 20px; color: white; display: inline-block;}
.mainpage button:hover {background: #ff0000; cursor: pointer;}
